.contact-preview {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
  padding: 1rem;
  cursor: pointer;
  transition: all 0.4s;
 a:link {color:white;
   text-decoration: none;

}
    p {color:white;
        text-decoration: none;
    }
  
  &:hover {
    box-shadow: 0 0 2px 2px grey;
  }

  img {
    width: 50px;
  }
}
