.contact-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;

  img {
    width: 50px;
  }

  p {
    width: 500px;
    border-top: 2px solid white;
    padding-top: 10px;
  }
}

.contact-details-container {
  button {
    color: white;
  }

  .transfer-funds {
    border-top: 2px solid white;
    margin-top: 10px;
    p {
      margin-bottom: 20px;
      span {
        color: gold;
      }
    }

    button{
      margin-left:10px;
      height: 1.5em
    }
  }
}
